import path from 'path';
import { cli, log } from '@bb-cli/base';
import { name as packageName, version } from '../../package.json';

log.heading = `[${packageName}]`;
/**
* ------------------------------------------------------------------------
* Git-style sub-commands definition
* ------------------------------------------------------------------------
*/

cli
  .version(version)
  .description('Quickly scaffold new Backbase components,' +
               'such as widgets, models, ui-components, etc.')
  .command('item <name>', 'Generate an item from generator name', { isDefault: true })
  .command('list [scope]', 'List available generators installed')
  // .command('install [...generators]', 'Installing generators from registry')
  // .command('search', 'Search for specific generators in registry')
  .man(path.resolve(__dirname, '../../man/bb-generate.md'))
  .parse(process.argv);
